body {
  background-color: #b6e0f1;
  font-family: Arial, sans-serif;
}

.general {
  text-align: center;
  color: #0e11d3;
  font-size: 26px;
}

.search-form {
  max-width: 600px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.input-size {
  border: 2px solid #1c7ed6;
  border-radius: 4px;
  flex: 1;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

.button-search {
  color: #f8f404;
  cursor: pointer;
  background-color: #0e11d3;
  border: 2px solid #1c7ed6;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 4px;
  flex-direction: column;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 5px rgba(28, 3, 247, .5);
}

.photo-card img {
  width: 100%;
  height: 160px;
  object-fit: cover;
  border-radius: 4px;
}

.info {
  flex-direction: column;
  margin-top: 10px;
  display: flex;
}

.info-item {
  margin-bottom: 5px;
}

.load-more {
  color: #f8f404;
  cursor: pointer;
  background-color: #0e11d3;
  border: 2px solid #1c7ed6;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  display: none;
}

